import {mapState} from "vuex";
import uPopup from '../../u-popup/u-popup.vue';

export default {
    name: "u-authorized-iphone",
    computed: {
        ...mapState({
            _app_config: state => state.mallConfig
        }),
        showPhone() {
            return this.$store.state.user.info;
        },
        img() {
            let img = '';

            img = this._app_config.__wxapp_img.mall.icon_wechat;







            return img;
        }
    },
    data() {
        return {

            code: null,

            show: false
        };
    },
    watch: {
        showPhone: {
            handler(newVal) {
                newVal && this.$validation.isEmpty(newVal.mobile) ? this.show = true : this.show = false;
            },
            immediate: true
        }
    },
    created() {

        let _this= this;
        uni.login({
            scopes: 'auth_base',
            success(res) {
                if (res.errMsg === 'login:ok') {
                    _this.code = res.code;
                }
            }
        })

    },
    destroyed() {
        this.show = false;
    },
    methods: {

        getPhoneNumber(e) {
            if (e.detail.errMsg === 'getPhoneNumber:fail user deny') {
                this.show = false;
                return false
            };
            this.$request({
                method: 'post',
                url: this.$api.phone.binding,
                data: {
                    encryptedData: e.detail.encryptedData,
                    iv: e.detail.iv,
                    code: this.code
                }
            }).then(() => {
                this.show = false;
                this.$store.dispatch('user/refresh');
            }).fail( res => {
                // this.show = false;
            }) 
        },























    },
    components: {
        uPopup
    }
}
